import React from 'react'
import { Row, Col, Container } from 'reactstrap'
import './style.scss'
import sd from '_assets/sd.png'


function SoDo(props) {
  return (
    <div className='pt-5 pb-5'>
      <Container>
        <Row>
          <Col xs={12} className='text-center'>
            <h4><b> S  C CU T CHC </b></h4>
          </Col>
          <Col md={12}>
            <img className='w-100' src={sd} alt='gt' />
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default SoDo
