import React, { useEffect, useState } from 'react'
import PrivateRouter from 'layout/privateRouter'
import { Row, Col, Container, InputGroup, Input } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import { request } from '_helpers/request'
import Paging from '_components/paging'
import searchIcon from '_assets/search-white.svg'
import './style.scss'
import Item from '_components/news-item'


function TinTuc(props) {
  const history = useHistory()
  const [newsObj, setNewsObj] = useState([])
  const [page, setPage] = useState(1)
  const [title, setTitle] = useState('')

 useEffect(() => {
   const params = { page: 1, size: 10 }
   request.get(`api/WebLink/client-search-new`, { params })
   .then(res => setNewsObj(res))
 }, [])
 function search(p, title1) {
   setPage(p + 1)
   const params = { page: p + 1, size: 10, title: title1 || title }
   request.get(`api/WebLink/client-search-new`, { params })
   .then(res => setNewsObj(res))
 }
 function goToDetail(e) {
  window.open(e.urL_LINK, "_blank")
 }

  return (
    <PrivateRouter {...props}>
      <Container>
        <div className='to-home link-page'>
          <i className="fa fa-home to-home-content" aria-hidden="true" onClick={() => history.push('/')}></i> / { 'Tin tc  y' }
        </div>
        <InputGroup className='mt-3 mb-3 input-wrapper'>
          <Input placeholder="Tm kim"
            onChange={(e) => setTitle(e.target.value)}
            className='inputField'
            onKeyDown={(e) => {
              if (e.which === 13) {
                search(0)
              }
            }}
          />
          <div className='btnSearch' onClick={() => search(0)}>
            <img src={searchIcon} className={'searchIcon'} alt='search-icon'/>
          </div>
        </InputGroup>
        <Row>
          {
            newsObj?.results?.map((e) =>
            <Col md={6} key={e.id}>
              <Item noIm showDes data={e} goDetail={() => goToDetail(e)}/>
            </Col>
          )
          }

        </Row>
      </Container>
      <br />
      <Paging data={newsObj} action={search} />
    </PrivateRouter>
  )
}

export default TinTuc
