import React from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import classes from './styles.module.scss'

const Paging = (props) => {
  let array = []
  const current = props?.data?.currentPage - 1
  const total = props?.data?.pageCount
  if (total < 6) {
    array = new Array(total)
    for (let i = 1; i < total + 1; i += 1) {
      array.push(i)
    }
  } else {
    if (current < 3) {
      array = [1,2,3,4,5, '...']
    } else if (current > total - 4) {
      array = ['....', total - 4, total - 3, total - 2, total - 1, total]
    } else {
      array = ['....', current - 1, current, current + 1, current + 2, current + 3, '...' ]
    }
  }
  const jumpPage = (e) => {
    if (e === '...') {
      props.action(array[array.length - 2])
    } else if (e === '....') {
      props.action(array[1] - 2)
    } else {
      props.action(e - 1)
    }
  }
  return (
    <Pagination aria-label="Page navigation example"
      className={classes.pagination}
    >
      {
        /*
        <PaginationItem disabled={current === 0}>
        <PaginationLink first onClick={() => props.action(0)} />
        </PaginationItem>
        */
      }
      <PaginationItem disabled={current === 0} className={classes.pageItem}>
        <PaginationLink previous onClick={() => props.action(current - 1)}/>
      </PaginationItem>
      { /*
        array.map((e, i) =>
        <PaginationItem active={e === current + 1} key={i}>
          <PaginationLink onClick={() => jumpPage(e)}>
            {e || ''}
          </PaginationLink>
        </PaginationItem>
      )
      */
      }
      <PaginationItem disabled={current === total - 1} className={classes.pageItem}>
        <PaginationLink next onClick={() => props.action(current + 1)} />
      </PaginationItem>
      {
        /*
        <PaginationItem disabled={current === total - 1}>
        <PaginationLink last onClick={() => props.action(total - 1)} />
        </PaginationItem>
        */
      }
    </Pagination>
  );
}

export default Paging
