import React, { useEffect, useState } from 'react'
import './App.scss'
// import './style/style.scss'
import { BrowserRouter, Route, Redirect } from 'react-router-dom'
import { AnimatedSwitch } from 'react-router-transition'
import { ToastContainer } from 'react-toastify'
import {request} from '_helpers/request'
import Home from './pages/Home'
import DuocCT from './pages/DuocCT'
import Search from './pages/DuocCT/search'
import Detail from './pages/DuocCT/detail'
import TinTuc from './pages/TinTuc'
import LienHe from './pages/LienHe'
import GioiThieu from './pages/GioiThieu'
import TinTucDetails from './pages/TinTuc/detail'
import NoMatch from './pages/NoMatch'
import Documents from 'pages/Documents'
import Video from 'pages/Video'


function mapStyles(styles) {
  return {
    opacity: styles.opacity,
    transition: '0.2s'
  };
}
function App() {
  const [menu, setMenu] = useState([])
  const [links, setLinks] = useState([])
  useEffect(() => {
    request.get('api/menu/client-menu')
    .then(res => {
      setMenu(res)
    })
    request.get('api/weblink/client-search-link?page=1&size=20')
    .then(res => {
      setLinks(res.results)
    })
  }, [])
  return (
    <div className="App">
      <ToastContainer />
      <BrowserRouter basename="/">
          <AnimatedSwitch
            atEnter={{ opacity: 0 }}
            atLeave={{ opacity: 0 }}
            atActive={{ opacity: 1 }}
            mapStyles={mapStyles}
            className="switch-wrapper"
          >
            <Route exact path="/" name="Home" component={(props) => <Home menu={menu} links={links} {...props} />} />
            { /* <Route exact path="/duoc-co-truyen" name="DuocCT" component={(props) => <DuocCT menu={menu} links={links} {...props} />} /> */}
            <Route exact path="/tin-tuc-do-day" name="Tin Tuc" component={(props) => <TinTuc menu={menu} links={links} {...props} />} />
            <Route exact path="/lien-he" name="Lin h" component={(props) => <LienHe menu={menu} links={links} {...props} />} />
            <Route path="/gioi-thieu" name="Gii thiu" component={(props) => <GioiThieu menu={menu} links={links} {...props} />} />
            <Route exact path="/tin-tuc-do-day/:id" name="Tin Tuc" component={(props) => <TinTucDetails menu={menu} links={links} {...props} />} />
            <Route exact path="/search" name="search" component={(props) => <Search menu={menu} links={links} {...props} />} />
            <Route exact path="/video" name="Video" component={(props) => <Video menu={menu} links={links} {...props} />} />
            <Route path="/van-ban/:sub/:id" name="Vn bn" component={(props) => <Detail menu={menu} links={links} {...props} documentDetail />} />
            <Route path="/van-ban" name="Vn bn" component={(props) => <Documents menu={menu} links={links} {...props} />} />
            <Route exact path="/:sub" name="abc" component={(props) => <DuocCT menu={menu} links={links} {...props} />} />
            <Route exact path="/:sub/:id" name="detail" component={(props) => <Detail menu={menu} links={links} {...props} />} />
            <Redirect to="/" />
            <Route component={NoMatch} />
          </AnimatedSwitch>
      </BrowserRouter>
    </div>
  );
}

export default App;
