import React from 'react'
import Slider from 'react-slick'
import classNames from 'classnames'
import classes from './SlideCategories.module.scss'

const settings = {
  className: "slider variable-width",
  dots: false,
  infinite: false,
  slidesToShow: 1,
  slidesToScroll: 3,
  variableWidth: true,
  responsive: [
    {
      breakpoint: 767,
      settings: {
        slidesToScroll: 1,
      }
    },
  ]
};

const SlideCategories = ({ categories, title, setTitle, search }) => {
  return (
    <div className={classes.container}>
      <Slider {...settings}>
        {categories?.map(e =>
          <div key={e.categorY_ID}
            className={classes.categoryWrapper}
          >
            <div
              className={classNames(classes.cate, e.desc === title && classes.catActive)}
              onClick={() => { setTitle(e.desc); search(0, e.desc) }}
              > {e.desc}
            </div>
          </div>
        )}
      </Slider>
    </div>
  )
}

export default SlideCategories
