import React, { useEffect, useState } from 'react'
import { Row, Col, Container,Card, CardBody} from 'reactstrap'
import './style.scss'
import { request } from '_helpers/request'
import Item from '_components/news-item'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function ThanhTich(props) {
  const [news, setNews] = useState([])
  useEffect(() => {
    request.get(`api/News/client-search?subMenu=${4}&page=1&size=1000`)
    .then(res => setNews(res.results))
  }, [])
  return (
    <div style={{ paddingBottom: '40px'}} className='pt-5'>
      <Container>
        <Row>
          <Col xs={12} className='text-center text-uppercase'>
            <h4><b> Thnh Tch Ni Bt </b></h4>
          </Col>
        </Row>
        <Row>
          {
            news.map((e, i) =>
            <Col xs={12} sm={4} className='p-3' key={i}>
              <Card style={{height: '100%'}}>
                <CardBody className='thanh-tich-car-body'>
                  <Item data={e} showDes paddingContent/>
                </CardBody>
              </Card>
            </Col>
          )
          }
        </Row>
      </Container>
    </div>
  )
}

export default ThanhTich
