import React, { useEffect, useState } from 'react'
import { request } from '_helpers/request'
import classNames from 'classnames'
import moment from 'moment'
import classes from './Documents.module.scss'
import PrivateRouter from 'layout/privateRouter'
import GooglePaging from '_components/GooglePaging'
import { useHistory } from 'react-router'
import { InputGroup, Input } from 'reactstrap'
import searchIcon from '_assets/search-white.svg'

const Documents = (props) => {
  const history = useHistory()
  const { menu, location } = props
  const [newsObj, setNewsObj] = useState([])
  const [ menuObj, setMenuObj ] = useState({})
  const [ title, setTitle ] = useState('')
  const [allCate, setAllCate] = useState([])

  const getDocuments = (p = 0, title) => {
    const params = {
      page: p + 1,
      size: 10,
      menu: menuObj?.menU_ID,
      submenu: menuObj?.subMenu?.submenU_ID,
      title
    }
    request.get(`api/news/client-search`, { params })
      .then(res => setNewsObj(res))
  }

  useEffect(() => {
    if (menuObj?.menU_ID) {
      getDocuments(0)
    }
  }, [menuObj?.menU_ID, menuObj.subMenu?.submenU_ID])

  useEffect(() => {
    const selectedMenu = menu.find((item) => item.name === "VANBAN")
    const selectedSubMenu = selectedMenu?.submenu?.find((item) => item.urL_NAME === location.pathname.slice(1))
    if (!selectedSubMenu) {
      let allCate = []
       for (let j = 0; j < selectedMenu?.submenu.length; j += 1) {
         const smn = selectedMenu.submenu[j]
         allCate = allCate.concat(smn.category)
       }
       setAllCate(allCate)
    }
    setMenuObj({
      desc: selectedMenu?.desc,
      menU_ID: selectedMenu?.menU_ID,
      name: selectedMenu?.name,
      subMenu: selectedSubMenu,
      urL_NAME: selectedMenu?.urL_NAME
    })

  }, [location.pathname])

  return (
    <PrivateRouter dct {...props}>
      <div className={classes.container}>
      <div className={classNames('to-home mb20', classes.mb20)}>
        <i className="fa fa-home to-home-content" aria-hidden="true" onClick={() => history.push('/')}></i> &nbsp;
        <span className='link-page' onClick={() => history.push(`/${menuObj.urL_NAME}`)}> / { menuObj.desc } </span> { menuObj.subMenu && <span className='link-page' onClick={() => history.push(`/${menuObj.subMenu.urL_NAME}`)}> / { menuObj.subMenu?.description } </span>}
      </div>

     

      <InputGroup className='mt-3 input-wrapper'>
        <Input value={title}
          placeholder="Tm kim"
          onChange={(e) => setTitle(e.target.value)}
          className='inputField'
          onKeyDown={(e) => {
            if (e.which === 13) {
              getDocuments(0, title)
            }
          }}
        />
        <div className='btnSearch' onClick={() => getDocuments(0, title)}>
          <img src={searchIcon} className={'searchIcon'} alt='search-icon'/>
        </div>
      </InputGroup>

      {
        menuObj?.subMenu && menuObj?.subMenu?.category &&
        <div className='d-flex mt-2 flex-wrap'>
          {
            menuObj?.subMenu?.category?.map(e =>
              <div
                className={classNames('cate p-2 mr-1 mb-1', e.desc === title && 'cat-active')}
                onClick={() => { setTitle(e.desc); getDocuments(0, e.desc) }}
                key={e.categorY_ID}> {e.desc}
              </div>)
          }
        </div>
      }

      {
        !menuObj?.subMenu && allCate &&
        <div className='d-flex mt-2 flex-wrap'>
          {
            allCate.map(e =>
              <div
                className={classNames('cate p-2 mr-1 mb-1', e.desc === title && 'cat-active')}
                onClick={() => { setTitle(e.desc); getDocuments(0, e.desc) }}
                key={e.categorY_ID}> {e.desc}
              </div>)
          }
        </div>
      }

        <div className={classNames('table-responsive table mt20')}>
          <table>
            <thead>
              <tr className="tbHead">
                <th>
                  #
                </th>
                <th>
                  M vn bn
                </th>
                <th>
                  Tn vn bn
                </th>
                <th>
                  Ngy ban hnh
                </th>
              </tr>
            </thead>

            <tbody>
              {newsObj?.results &&
                newsObj.results.map((item, i) => (
                  <tr key={item.newS_ID}
                    className="tbRow"
                    onClick={() => history.push(item.submenu_url_name ? `/${item.submenu_url_name}/${item.newS_ID}` : `/${item.menu_url_name}/${item.newS_ID}`)}
                  >
                    <td>
                      <strong>{ i + 1 }</strong>
                    </td>
                    <td>
                      { item.titlE_CODE }
                    </td>
                    <td>
                      { item.title }
                    </td>
                    <td className='text-center'>
                      { moment(item.issuE_DATE).format('DD/MM/YYYY') }
                    </td>
                  </tr>
                ))
              }
            </tbody>
          </table>
        </div>
        <GooglePaging pageInfo={{
          page: newsObj?.currentPage,
          totalPages: newsObj?.pageCount
        }}
        changePage={({page}) => {
          getDocuments(page - 1, title)
        }}
        />
      </div>
    </PrivateRouter>
   
  )
}

export default Documents
