import React, { useEffect, useState } from 'react'
import PrivateRouter from 'layout/privateRouter'
import { useHistory, useRouteMatch, NavLink } from 'react-router-dom'
import { request } from '_helpers/request'
import { Switch, Route } from 'react-router-dom'
import './style.scss'
import Notification from '_components/Notification'
import About from './About'
import LanhDao from './lanhdao.js'
import SoDo from './sodo.js'
import ThanhTich from './thanhtich.js'
import { Container } from 'reactstrap'

function GioiThieu(props) {
  const { path } = useRouteMatch()
  const history = useHistory()
  const [config, setConfig] = useState({})
  useEffect(() => {
    request.get('api/confugration')
    .then(res => {
      var obj = res.reduce(function(acc, cur) {
        acc[cur.confugratioN_CODE] = cur
        return acc
      }, {})
      setConfig(obj)
    })
  }, [])
 function submit (data) {
   request.post('api/contract', { data })
   .then(res => {
     Notification.success(res.description)
   })
   .catch()
 }

  return (
    <PrivateRouter {...props} about home>
      <div className={'about-container'}>
        <Container>
        <div className={'about-menus'}>
          <NavLink className='about-menu'
            to='/gioi-thieu'
            exact
            activeClassName='about-menu-active'
          >
            Lch s pht trin
          </NavLink>
          <NavLink className='about-menu'
            to='/gioi-thieu/lanh-dao'
            exact
            activeClassName='about-menu-active'
          >
            Lnh o qua cc thi k
          </NavLink>
          <NavLink className='about-menu'
            to='/gioi-thieu/so-do-to-chuc'
            exact
            activeClassName='about-menu-active'
          >
            S  t chc
          </NavLink>
          <NavLink className='about-menu'
            to='/gioi-thieu/thanh-tich'
            exact
            activeClassName='about-menu-active'
          >
            Thnh tch ni bt
          </NavLink>
        </div>
        </Container>
        <Switch>
          <Route path={`${path}`}
            component={About}
            exact
          />
          <Route path={`${path}/lanh-dao`}
            component={LanhDao}
          />
          <Route path={`${path}/so-do-to-chuc`}
            component={SoDo}
          />
          <Route path={`${path}/thanh-tich`}
            component={ThanhTich}
          />
        </Switch>
        
      </div>
      
    </PrivateRouter>
  )
}

export default GioiThieu
