import React from 'react'
import { Row, Col, Container, Button } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import logo from '_assets/no-image.jpg'
import './style.scss'
import dayjs from 'dayjs'
import { FileURL } from '_helpers/constants'

function Item({data, showDes}) {
  const history = useHistory()
  return (
    <Container className='mb-16 text-justify p-0'>
      <Row>
        <Col sm={3}>
          <img className='w-100' src={data.avatar ? `${FileURL}${data.avatar}` : logo} alt='anh' />
        </Col>
        <Col sm={9}>
          <div className='mt-1 news-item2-title' style={{ cursor: 'pointer' }} onClick={() => history.push(data.submenu_url_name ? `${data.submenu_url_name}/${data.newS_ID}` : `${data.menu_url_name}/${data.newS_ID}`)}>{data.title}</div>
          <div className='mt-1'><p  dangerouslySetInnerHTML={{ __html: data.shorT_CONTENT }} /></div>
          <i className="fa fa-clock-o" aria-hidden="true" style={{ color: '#999999' }}></i> &nbsp;<small>{dayjs(data.publisH_DATE || data.approveD_DATE).format('DD/MM/YYYY')}</small>
          &nbsp;
          &nbsp;
          &nbsp;
          <i className="fa fa-eye" aria-hidden="true" style={{ color: '#999999' }}></i>
          &nbsp;
          <small className='view-more' onClick={() => history.push(data.submenu_url_name ? `${data.submenu_url_name}/${data.newS_ID}` : `${data.menu_url_name}/${data.newS_ID}`)}> Xem thm </small>
        </Col>
      </Row>
    </Container>
  )
}
export default Item
