import React from 'react'
import { Row, Col, Container } from 'reactstrap'
import './style.scss'
import classes from './lanhdao.module.scss'
import image1 from '_assets/nguyen-the-thinh.jpeg'
import image2 from '_assets/anh-ngoc.jpg'
import image3 from '_assets/nguyen-ngoc-tuan.jpg'

const leaders = [{
  avatar: image1,
  fullname: 'PGS.TS. NGUYN TH THNH',
  gender: 'MALE',
  position: 'Cc Trng',
  description: 'Cc Trng Cc Y, Dc C Truyn.'
}, {
  avatar: image3,
  fullname: 'ThS.BS. NGUYN NGC TUN',
  gender: 'MALE',
  position: 'Ph Cc Trng',
  description: 'Ph Cc Trng Cc Y, Dc C Truyn'
}, {
  avatar: image2,
  fullname: 'TS.DS. TRN MINH NGC',
  gender: 'MALE',
  position: 'Ph Cc Trng',
  description: 'Ph Cc Trng Cc Y, Dc C Truyn.'
}]

function LanhDao(props) {
  return (
    <div style={{ paddingBottom: '60px'}} className='pt-5'>
      <Container>
        <Row>
          <Col xs={12} className='text-center'>
            <h4><b> LNH O NG NHIM </b></h4>
          </Col>
          <Col xs={12} className='text-center mb-20' style={{marginBottom: '24px'}}>
            <b> Danh sch nhng ngi c nh nc tin tng giao trng trch lo li con thuyn ca cc Y, Dc c truyn </b>
          </Col>
        </Row>
        { leaders.map((item, i) => (
          <div className={classes.row}
            key={i}
          >
            <img src={item.avatar} className={classes.image} alt='img'/>
            <div className={classes.info}>
              <p className={classes.name}>
                ({item.gender === 'MALE' ? 'ng' : 'B'})
                <strong>
                  { item.fullname } 
                </strong>
              </p>
              <p className={classes.position}>
                { item.position }
              </p>
              <p className={classes.description}>
                { item.description }
              </p>
            </div>
          </div>
        )) }
        
      </Container>
    </div>
  )
}

export default LanhDao
