import { APP_API } from './constants'
export function cleanEmptyKey(obj) {
  const newObj = { ...obj }
  var propNames = Object.getOwnPropertyNames(obj);
  for (var i = 0; i < propNames.length; i++) {
    var propName = propNames[i];
    if (obj[propName] === null || obj[propName] === undefined) {
      delete newObj[propName];
    }
  }
  return newObj
}

export function transformValue(data, id='id', label='description') {
  return data.map((e) => {
    let a = {}
    a = { value: e[id], label: e[label] }
    return a
  })
}

export function formatNumber(value) {
  if (value === 0) {
    return '0'
  }
  if (value && value.toString().replace(/[^-0-9]/g, '').replace(/,/g, '')) {
    return parseInt(value.toString().replace(/[^-0-9]/g, '').replace(/,/g, ''), 10).toLocaleString('en-US')
  }
  return ''
}

export function unFormatNumber(value) {
  if (value || value === 0) {
    return parseInt(value.toString().replace(/,/g, ''), 10)
  }
  return ''
}

export function queryStringToObject(query) {
  if (!query || query.length === 0) {
    return {}
  } else {
    const arr = query.substring(1).split('&')
    const object = {}
    for (let i = 0; i < arr.length; i += 1) {
      object[arr[i].split('=')[0]] = arr[i].split('=')[1]
    }
    return object
  }
}
function traverseAndFlatten(currentNode, target, flattenedKey) {
    for (var key in currentNode) {
        if (currentNode.hasOwnProperty(key)) {
            var newKey;
            if (flattenedKey === undefined) {
                newKey = key;
            } else {
                newKey = flattenedKey + '.' + key;
            }

            var value = currentNode[key];
            if (typeof value === "object") {
                traverseAndFlatten(value, target, newKey);
            } else {
                target[newKey] = value;
            }
        }
    }
}

export function flatten(obj) {
    var flattenedObject = {};
    traverseAndFlatten(obj, flattenedObject);
    return flattenedObject;
}
export function imagesPath(path){
  return `${APP_API}/${path}`
}