import React, { useEffect, useState } from 'react'
import PrivateRouter from 'layout/privateRouter'
import { Row, Col, Button, Container } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import { request } from '_helpers/request'
import './style.scss'
import Validation from '_components/final-form/validation'
import createDecorator from 'final-form-focus'
import CustomField from '_components/final-form/customField'
import { Form, Field } from 'react-final-form'
import Notification from '_components/Notification'

const focusOnErrors = createDecorator()
function LienHe(props) {
  const history = useHistory()
  const [config, setConfig] = useState({})
  useEffect(() => {
    request.get('api/confugration')
    .then(res => {
      var obj = res.reduce(function(acc, cur) {
        acc[cur.confugratioN_CODE] = cur
        return acc
      }, {})
      setConfig(obj)
    })
  }, [])
 function submit(data) {
   request.post('api/contract', { data })
   .then(res => {
     Notification.success(res.description)
   })
   .catch()
 }
 const mailto = `mailto:${config?.EMAIL?.value}`
 const tel = `tel:${config?.PHONE?.value}`
  return (
    <PrivateRouter {...props} home>
      <Container>
        <Row>
          <Col xs={12} className='text-center'>
            <h2 className='mb40'><b> LIN H </b></h2>
          </Col>
          <Col md={6}>
            <Form
              onSubmit={submit}
              decorators={[ focusOnErrors ]}
              render={
                ({form, handleSubmit}) => (
                  <form onSubmit={handleSubmit}>
                    <Row>
                      <Col xs={6}>
                        <Field
                          name='name'
                          component={CustomField}
                          placeholder='in h v tn'
                          validate={Validation.composeValidators(Validation.required, Validation.person_name)}
                          className={'customInput'}
                        />
                      </Col>
                      <Col xs={6}>
                        <Field
                          name='email'
                          component={CustomField}
                          placeholder='Email'
                          validate={Validation.composeValidators(Validation.required, Validation.email)}
                          className={'customInput'}
                        />
                      </Col>
                      <Col xs={12}>
                        <Field
                          name='title'
                          component={CustomField}
                          placeholder='Tiu '
                          validate={Validation.composeValidators(Validation.required)}
                          className={'customInput'}
                        />
                      </Col>
                      <Col xs={12}>
                        <Field
                          name='content'
                          component={CustomField}
                          type='textarea'
                          placeholder='Ni dung'
                          validate={Validation.composeValidators(Validation.required)}
                        />
                      </Col>
                      <Col xs={12} className='pb-2 text-center'>
                        <Button
                          color='danger'
                          type='submit'
                          className='mt-3 text-uppercase btn-send'
                        >
                        <i className="fa fa-paper-plane" aria-hidden="true"></i>
                        &nbsp;
                        &nbsp;
                        Gi ngay
                        </Button>
                      </Col>
                    </Row>
                  </form>
                )
              }
            >
            </Form>
          </Col>
          <Col md={6}>
            <Row>
              <Col md={12} className='text-left'>
                <div className='mt-2'><b className='f-t1'> CC QUN L </b> </div>
                <div><b> Y DC C TRUYN </b> </div>
              </Col>
              <Col md={12} className='text-left'>
                <div className='mb10'><i> a ch lin h </i> </div>
                <div className='mb10'><i className="fa fa-map-marker" aria-hidden="true"></i> &nbsp; a ch: {config?.ADDRESS?.value}</div>
                <div className='mb10'><i className="fa fa-phone" aria-hidden="true"></i> &nbsp; in thoi: <a href={tel}>{config?.PHONE?.value}</a></div>
                <div className='mb10'><i className="fa fa-fax" aria-hidden="true"></i> &nbsp; Fax: {config?.FAX?.value}</div>

                <div className='mb10'><i className="fa fa-envelope" aria-hidden="true"></i> &nbsp; Email: <a href={mailto}>{config?.EMAIL?.value}</a> </div>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row>
          <Col xs={12}>
              <div className="mapouter map">
                <div className="gmap_canvas">
                  <iframe
                    title='ggmap'
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d5473.4312217937295!2d105.81875634683443!3d21.028871598083633!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3135ab76ab560c29%3A0x253637caebea1353!2zQ-G7pWMgUXXhuqNuIEzDvSBExrDhu6Nj!5e0!3m2!1sen!2sus!4v1625208074683!5m2!1sen!2sus"
                    height="300px"
                    width="100%"
                    allowFullScreen=""
                    loading="lazy"></iframe>
                  </div>
                </div>
          </Col>
        </Row>
      </Container>
    </PrivateRouter>
  )
}

export default LienHe
