import React, { useEffect, useState } from 'react'
import PrivateRouter from 'layout/privateRouter'
import { Row, Col } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import { request } from '_helpers/request'
import dayjs from 'dayjs'
import './style.scss'
import { FileURL } from '_helpers/constants'


function DuocCTDetail(props) {
  const history = useHistory()
  const [newsObj, setNewsObj] = useState([])
  const [menuObj, setMenuObj] = useState({})
 useEffect(() => {
   window.scrollTo({ top: 0 });
   request.get(`api/news/${props.match.params.id}`)
     .then(res => setNewsObj(res.data))

 }, [props.match.params.id])
 useEffect(() => {
   if (props.documentDetail) {
    const selectedMenu = props.menu.find((item) => item.name === "VANBAN")
    const selectedSubMenu = selectedMenu?.submenu?.find((item) => props.location.pathname.indexOf(item.urL_NAME) !== -1)
    
    
    setMenuObj({ menu: {
      id: selectedMenu?.menU_ID,
      name: selectedMenu?.desc,
      url: selectedMenu?.urL_NAME,
    }, submenu: {
      id: selectedSubMenu?.menU_ID,
      name: selectedSubMenu?.description,
      url: selectedSubMenu?.urL_NAME
    }})
   } else {
    for (let i = 0; i < props.menu.length; i += 1) {
      const mn = props.menu[i]
      if (mn.urL_NAME === props.match.params.sub) {
        setMenuObj({ menu: { id: mn.menU_ID, name: mn.desc, url: mn.urL_NAME }})
        return
      }
      if (mn.submenu) {
        for (let j = 0; j < mn.submenu.length; j += 1) {
          const smn = mn.submenu[j]
          if (smn.urL_NAME === props.match.params.sub) {
            setMenuObj({ menu: { id: mn.menU_ID, name: mn.desc, url: mn.urL_NAME }, submenu: { id: smn.menU_ID, name: smn.description, url: smn.urL_NAME }})
            return
          }
        }
      }
    }
   }
   
 }, [props.menu, props.match.params.sub])

  return (
    <PrivateRouter dct {...props}>
      <div className='to-home'>
        <i className="fa fa-home to-home-content" aria-hidden="true" onClick={() => history.push('/')}></i> &nbsp;
        <span className='link' onClick={() => history.push(`/${menuObj.menu.url}`)}> / { menuObj.menu?.name } </span>
        { menuObj.submenu && <span className='link' onClick={() => history.push(`/${menuObj.submenu.url}`)}> / { menuObj.submenu?.name } </span>}
        / {newsObj?.titlE_CODE?.substring(0,20)}...
      </div>
      <Row className='mt-3'>
        <Col xs={12}>
          <h5>{newsObj.title}</h5>
        </Col>
        <Col xs={12}>
          <div className='border-bottom mb-3'>
            <small>{dayjs(newsObj.publisH_DATE || newsObj.issuE_DATE).format('DD/MM/YYYY HH:mm:ss')}</small>
          </div>
        </Col>
        <Col xs={12} className='mb-3'>
          <b>{newsObj.description}</b>
        </Col>
        <Col xs={12}>
          <div dangerouslySetInnerHTML={{ __html: newsObj.contents }} />
        </Col>
        {
          newsObj.documentAttacks &&
          <Col xs={12}>
            <b className='documentTitle'> Ti liu nh km: &nbsp; </b>
            {
              newsObj.documentAttacks.map((e) =>
              <a className='documentLink' key={e.documenT_ID} href={`${FileURL}${e.documenT_LINK}`} download={e.documenT_NAME}>{e.documenT_NAME} &nbsp;</a>
              )
            }

          </Col>
        }

      </Row>
    </PrivateRouter>
  )
}

export default DuocCTDetail
