import React from "react"
import { Row, Col, Button, Container } from 'reactstrap'
// import dayjs from 'dayjs'
import { useHistory } from 'react-router-dom'
import classnames from 'classnames'
import '../style.scss'
import classes from './style.module.scss'

function Item(props) {
  const history = useHistory()
  return (
    <Container
      className={classnames('mb-2 bg-white p-5 text-center', classes.item)}
      style={{ cursor: 'pointer' }}
    >
        <Row className={classes.detail1}>
          <Col xs={12}>
            <img src={props.data.img} alt='anh' style={{ maxWidth: '100%' }} />
            <div className='mt-2 text-uppercase'><b>{props.text}</b></div>
          </Col>
        </Row>

        <Row className={classnames(classes.detail, 'p-md-3 p-xs-1')}>
          <Col md={5} className='d-md-block d-none'>
            <img style={{ maxWidth: '100%' }} src={props.data.img} alt='anh' />
          </Col>
          <Col xs={12} md={7} className='text-left'>
            {
              props.data.cate.map((e, i) => <div key={i} className={classes.cate} onClick={() => history.push(e.url)}>
                <i className="fa fa-caret-right" aria-hidden="true"></i> &nbsp;<small>{e.name}</small>
              </div>)
            }

          </Col>
        </Row>
    </Container>
  )
}
export default Item
