export const composeValidators = (...validators) => value =>
  validators.reduce((error, validator) => error || validator(value), undefined)
export const required = value => (value != null && `${value}`.length > 0 ? null : 'Vui lng nhp trng ny');
export const otp = value => (value.toString().length !== 6 ? 'OTP c 6 k t' : null);
export const email = value => (value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,6}$/i.test(value) ? 'Yu cu nhp ng nh dng email' : null);
export const mobile = value => (value && !/^0[1-9]\d{8}$/i.test(value) ? 'Yu cu nhp ng nh dng s in thoi' : null);
export const cmndCCCD = value => {
  if (!value) {
    return null;
  }
  if (!/^[0-9.]/i.test(value)) return 'CCCD/CMND phi bt u bng s hoc k t "."'
  if (!/^[0-9.]\d*$/i.test(value)) return 'CCCD/CMND ch cha s v k t "."'
  if (/^\./i.test(value) && value.length !== 10 && value.length !== 13) {
    return 'CCCD/CMND bt u bng du "." phi c 10 hoc 13 k t'
  }
  if (/^\d/i.test(value) && value.length !== 9 && value.length !== 12) {
    return 'CCCD/CMND bt u bng s phi c 9 hoc 12 k t'
  }
  return null;
}
export const person_name = value =>
  value &&
    !/^[A-Za-z ]{5,30}$/i.test(
      value
    )
    ? 'Vui lng nhp ng nh dng v t 5 n 30 k t'
    : null;
export const passport = value => (value && !/^[a-zA-Z0-9]{1,20}$/i.test(value)) ? 'H chiu ch cha ch ci v s c  di t 1-20 k t' : null

const exporter = {
  composeValidators, required, otp, email, cmndCCCD, person_name, mobile, passport
}
export default exporter
