import React from 'react'
import { UncontrolledCollapse, Button, CardHeader } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import './style.scss'

function Item({data, keyword}) {
  const id = `${keyword}`
  const toggler = `#${keyword}`
  const history = useHistory()
  return (

    <React.Fragment>
      <CardHeader className='d-flex p-1 menu-custom'>
        <Button className='btn-trans' onClick={() => history.push(data.url)}>
          <b className='text-uppercase'>{data.name}</b>
        </Button>
        <Button id={id} className='btn-trans text-right'>
          <i className="fa fa-caret-down" aria-hidden="true"></i>
        </Button>

      </CardHeader>
      <UncontrolledCollapse toggler={toggler}>
        {
          data.cate.map((e, i) =>
            <div
              onClick={() => {
                if (e.url.indexOf('http') !== -1) {
                  window.open(e.url)
                } else {
                  history.push(e.url)
                }
              }}
              className='pr-3 pl-3 pt-1 pb-1 border-bottom side-t menu-sub-menu'
              key={i}
              style={window.location.pathname === e.url ? { background: '#BFDDFF'} : {}}
            >
              {e.name}
            </div>)
        }
      </UncontrolledCollapse>
    </React.Fragment>
  )
}

export default Item
