import React, { useEffect, useState } from 'react'
import { request } from '_helpers/request'
import classes from './Video.module.scss'
import PrivateRouter from 'layout/privateRouter'
import classNames from 'classnames'
import ReactPlayer from 'react-player'
import GooglePaging from '_components/GooglePaging'

const Video = (props) => {
  const [newsObj, setNewsObj] = useState([])

  const [currentVideo, setCurrentVideo] = useState({})
  const [playing, setPlaying] = useState(false)

  const getVideos = async (p = 0) => {
    const result = await request.get(`api/News/top-view-by-type?type=2&page=${p + 1}&size=9`)
    setCurrentVideo(result.results[0])
    setNewsObj(result)
  }

  useEffect(() => {
    getVideos()
  }, [])

  return (
    <PrivateRouter dct {...props}>
      <div className={classes.container}>
      <div className={classes.currentVideo}>
      <div className={classes.videoWrapper}>
            { currentVideo?.documentAttacks
              && currentVideo?.documentAttacks.length > 0
              && <ReactPlayer url={currentVideo?.documentAttacks[0].documenT_LINK}
                  controls
                  className={classes.video}
                  width='100%'
                  height='100%'
                  playing={playing}
                  onPlay={() => setPlaying(true)}
                  onPause={() => setPlaying(false)}
              />
            }
            
          </div>
         
      </div>
      <h2 className={classes.title}>
            { currentVideo?.title }
          </h2>

      <p className={classes.subTitle}>
        Cc video Khc
      </p>
      <div className={classes.news}>
        { newsObj.results && newsObj.results.map((item) => (
          <div className={classNames(classes.new, currentVideo.newS_ID === item.newS_ID && classes.newActive)}
            key={item.newS_ID}
            onClick={() => {
              setCurrentVideo(item)
              setPlaying(true)
            }}
          >
            <div className={classes.thumbVideoWrapper}>
              <div className={classes.thumbVideo}>
                <ReactPlayer url={item?.documentAttacks[0]?.documenT_LINK}
                  className={classes.video}
                  width='100%'
                  height='100%'
                  playIcon={<div />}
                  light = {true}
                />
              </div>
              { playing
                && <div className={classes.playing}>
                ang pht
              </div>
              }
              
            </div>
            <p className={classes.newsTitle}>
              { item.title }
            </p>
          </div>
        )) }
      </div>
     
        <GooglePaging pageInfo={{
          page: newsObj?.currentPage,
          totalPages: newsObj?.pageCount
        }}
        changePage={({page}) => {
          getVideos(page - 1)
        }}
        />
      </div>
    </PrivateRouter>
   
  )
}

export default Video
