import React, { useEffect, useState } from 'react'
import PrivateRouter from 'layout/privateRouter'
import { InputGroup, Input } from 'reactstrap'
import { useHistory } from 'react-router-dom'
import { request } from '_helpers/request'
import Paging from '_components/paging'
import searchIcon from '_assets/search-white.svg'
import './style.scss'
import Item from '_components/news-item/index2'
import classNames from 'classnames'
import qs from 'qs'
import SlideCategories from './SlideCategories'


function DuocCT(props) {
  const history = useHistory()
  const [newsObj, setNewsObj] = useState([])
  const [menuObj, setMenuObj] = useState({})
  const [title, setTitle] = useState('')
  const [allCate, setAllCate] = useState([])

 useEffect(() => {
   window.scrollTo({ top: 0 });
   const params = { page: 1, size: 10, menu: menuObj?.menu?.id, submenu: menuObj?.submenu?.id }
   if (menuObj.menu) {
     request.get(`api/news/client-search`, { params })
     .then(res => setNewsObj(res))
   }

 }, [menuObj])

 useEffect(() => {
   setTitle('')
 }, [props.location.pathname])

 useEffect(() => {
   for (let i = 0; i < props.menu.length; i += 1) {
     const mn = props.menu[i]
     if (mn.urL_NAME === props.location.pathname.substring(1)) {
       setMenuObj({ menu: { id: mn.menU_ID, name: mn.desc, url: mn.urL_NAME }})
       let allCate = []
       for (let j = 0; j < mn.submenu.length; j += 1) {
         const smn = mn.submenu[j]
        
         allCate = allCate.concat(smn.category)
       }
       setAllCate(allCate)
       return
     }
     if (mn.submenu) {
       for (let j = 0; j < mn.submenu.length; j += 1) {
         const smn = mn.submenu[j]
         if (smn.urL_NAME === props.location.pathname.substring(1)) {
           console.log('test======>',smn.category)
           setMenuObj({ menu: { id: mn.menU_ID, name: mn.desc, url: mn.urL_NAME }, submenu: { id: smn.submenU_ID, name: smn.description, url: smn.urL_NAME, categories: smn.category }})
           return
         }
       }
     }
   }
 }, [props.menu, props.location?.pathname])

 function search(p = 0, title1) {
   const params = { page: p + 1, size: 10, menu: menuObj?.menu?.id, submenu: menuObj?.submenu?.id, title: title1 || title }
   request.get(`api/news/client-search`, { params })
   .then(res => setNewsObj(res))
 }

 useEffect(() => {
  const params = qs.parse(props.location.search, {
    ignoreQueryPrefix: true,
   })

   let categories = menuObj?.submenu?.categories || allCate

   if (params.danh_muc && categories) {
    const selectedCategory = categories.find((item) => item.urL_NAME === params.danh_muc)
    if (selectedCategory) {
      setTitle(selectedCategory.desc)
      search(0, selectedCategory.desc)
    }
   }

 }, [props.location.search, allCate, menuObj?.submenu?.categories])

  return (
    <PrivateRouter dct {...props}>
      <div className='to-home'>
        <i className="fa fa-home to-home-content" aria-hidden="true" onClick={() => history.push('/')}></i> &nbsp;
        <span className='link-page' onClick={() => history.push(`/${menuObj.menu.url}`)}> / { menuObj.menu?.name } </span> { menuObj.submenu && <span className='link-page' onClick={() => history.push(`/${menuObj.submenu.url}`)}> / { menuObj.submenu?.name } </span>}
      </div>
      <InputGroup className='mt-3 input-wrapper'>
        <Input value={title}
          placeholder="Tm kim"
          onChange={(e) => setTitle(e.target.value)}
          className='inputField'
          onKeyDown={(e) => {
            if (e.which === 13) {
              search(0)
            }
          }}
        />
        <div className='btnSearch' onClick={() => search(0)}>
          <img src={searchIcon} className={'searchIcon'} alt='search-icon'/>
        </div>
      </InputGroup>
      <SlideCategories categories={menuObj?.submenu?.categories || allCate}
        setTitle={setTitle}
        search={search}
        title={title}
      />
      <div className='mt-5' />
      {
        newsObj?.results?.map((e,i) => <Item data={e} key={i} />)
      }
      {
        !(newsObj?.results?.length > 0) &&
        <i className='text-center no-news'> Khng c bi vit </i>
      }
      <br />
      {
        (newsObj?.results?.length > 0) &&
        <Paging data={newsObj} action={search} />
      }
    </PrivateRouter>
  )
}

export default DuocCT
