import { toast } from 'react-toastify'

const properties = {
  position: "top-right",
  autoClose: 5000,
  progress: undefined,
  pauseOnHover: true,
  hideProgressBar: false,
}
toast.options = properties
export const error = (message) => {
  toast.error(message)
}

export const success = (message) => {
  toast.success(message)
}

export const warring = (message) => {
  toast.warn(message)
}

export default { error, success, warring }
