import React from "react";
import classnames from "classnames";
import moment from "moment";
import { Row, Col, Button, Container } from "reactstrap";
// import dayjs from 'dayjs'
import classes from "./style.module.scss";
import { BsSearch } from "react-icons/bs";

const daysOfWeek = [ "Ch nht", "Th hai", "Th ba", "Th t", "Th nm", "Th su", "Th by"]

function Section2({ data }) {
  return (
    <Container>
      <div className={classes.time_langue}>
        <div className={classes.time_boxsearch}>
          <div className="row">
            <div className="col-lg-6 col-md-6 col-sm-6 col-xs-6">
              <span>{daysOfWeek[moment().day()]}</span>, {moment().format('DD/MM/YYYY')}
            </div>
            <div
              className={classnames(
                "col-lg-6 col-md-6 col-sm-6 col-xs-6",
                classes.right1
              )}
            >
              {/* <a className="english-lang" href="https://dav.gov.vn/en">
                English
              </a> */}
              <div id="search" className={classes.nav_search_right}>
                <form
                  className="search-form row-item"
                  action="https://dav.gov.vn/tim-kiem-tin-tuc"
                  name="search_form"
                  id="search_form"
                  method="post"
                  onsubmit="javascript: submit_form_search();return false;"
                >
                  <div>
                    <input
                      type="text"
                      // defaultValue
                      placeholder="Nhp ni dung cn tm"
                      id="keyword"
                      name="keyword"
                      className={classes.form_input}
                    />
                    <button className={classes.search_button} type="submit">
                      <svg
                        stroke="currentColor"
                        fill="currentColor"
                        stroke-width="0"
                        viewBox="0 0 16 16"
                        height="1em"
                        width="1em"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"></path>
                      </svg>
                    </button>
                  </div>
                  <input type="hidden" name="module" defaultValue="news" />
                  {/* <input
                    type="hidden"
                    name="module"
                    id="link_search"
                    defaultValue="https://dav.gov.vn/tim-kiem-tin-tuc"
                  /> */}
                  {/* <input type="hidden" name="view" defaultValue="search" /> */}
                  {/* <input type="hidden" name="Itemid" defaultValue={20} /> */}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container>
  );
}

export default Section2;
