import React from 'react'
import {
  Card, CardBody, Button, UncontrolledCollapse,
  CardHeader
} from 'reactstrap'
import CATEGORY from '_helpers/constants'
import tin from '_assets/banner-tin.jpg'
import { useHistory } from 'react-router-dom'
import '../custom.scss'
import Item from './item'

function SideBar() {
  const history = useHistory()
  const arr = Object.keys(CATEGORY)
  return (
    <div>
      {
        arr.map((e, i) =>
        <Card className='mb-1' key={i}>
          <Item data={CATEGORY[e]} keyword={e} />
        </Card>)
      }
      <Card className='mb-1'>
        <CardHeader className='d-flex p-1 menu-custom' style={{ backgroundColor: '#e68700' }}>
          <Button id={'dvc1'} className='btn-trans d-flex justify-content-between align-items-center'>
            <b className='text-uppercase'>Dch V Cng Trc Tuyn</b>
            <i className="fa fa-caret-down" aria-hidden="true"></i>
          </Button>

        </CardHeader>
        <UncontrolledCollapse toggler={'#dvc1'}>
          <div className='p-2 dvc' onClick={() => window.open('https://dichvucong.gov.vn/p/home/dvc-trang-chu.html', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-globe" aria-hidden="true"></i> &nbsp;&nbsp;<i>Dch V Cng Trc Tuyn</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('https://dichvucong.gov.vn/p/phananhkiennghi/pakn-gui-pakn.html', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-envelope" aria-hidden="true"></i>&nbsp;&nbsp;<i>Phn nh kin ngh</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('https://dichvucong.gov.vn/p/home/dvc-tthc-thu-tuc-hanh-chinh.html', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-file" aria-hidden="true"></i>&nbsp;&nbsp;<i>Th tc hnh chnh</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('https://dichvucong.gov.vn/p/home/dvc-thanh-toan-truc-tuyen.html', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-credit-card" aria-hidden="true"></i>&nbsp;&nbsp;<i>Thanh ton trc tuyn</i>
          </div>
        </UncontrolledCollapse>
      </Card>
      <Card className='mb-1'>
        <CardHeader className='d-flex p-1 menu-custom' style={{ backgroundColor: '#e68700' }}>
          <Button id={'dvc2'} className='btn-trans d-flex justify-content-between align-items-center'>
            <b className='text-uppercase'>Dch V Cng Dc c truyn</b>
            <i className="fa fa-caret-down" aria-hidden="true"></i>
          </Button>

        </CardHeader>
        <UncontrolledCollapse toggler={'#dvc2'}>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-globe" aria-hidden="true"></i> &nbsp;&nbsp;<i>Dch V Cng Dc c truyn</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/congbothuoc', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-certificate" aria-hidden="true"></i>&nbsp;&nbsp;<i>ng k thuc</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/congbogiathuoc', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-money" aria-hidden="true"></i>&nbsp;&nbsp;<i>Qun l gi</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-handshake-o" aria-hidden="true"></i>&nbsp;&nbsp;<i>Thng tin u thu thuc</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-audio-description" aria-hidden="true"></i>&nbsp;&nbsp;<i>Thng tin qung co thuc</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-map-marker" aria-hidden="true"></i>&nbsp;&nbsp;<i>Cng tc dc a phng</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-gavel" aria-hidden="true"></i>&nbsp;&nbsp;<i>ISO</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-gavel" aria-hidden="true"></i>&nbsp;&nbsp;<i>Qun l cht lng thuc</i>
          </div>
          <div className='p-2 dvc' onClick={() => window.open('http://ydct-dichvucong.moh.gov.vn/#', '_blank')}>
            <i style={{ color: '#e68700' }} className="fa fa-thumbs-o-up" aria-hidden="true"></i>&nbsp;&nbsp;<i>Thc hnh tt-GxP</i>
          </div>
        </UncontrolledCollapse>
      </Card>

      <img className='w-100 mb-5' style={{ cursor: 'pointer' }} src={tin} alt='tin' onClick={() => history.push('/tin-tuc-do-day')} />
    </div>
  )
}

export default SideBar
