import axios from 'axios'
import Notification from '_components/Notification'
import { createBrowserHistory } from 'history'
import { APP_API } from './constants'
const history = createBrowserHistory()
export { APP_API };
export const APP_API1 = 'http://116.111.123.141:8021' // test
// export const APP_API = 'http://116.111.123.141:8021' // dev
const methods = ['GET', 'POST', 'PUT', 'DELETE']
let clientApi = {}


methods.forEach((method) => {
  const request = (url, { params, data } = {}, header = {}) => {
    return new Promise((resolve, reject) => {
      axios({
        method,
        url: `${APP_API}/${url}`,
        params,
        data,
        headers: {
          ...header,
        }
      }).then(response => {
        return resolve(response.data)
      }).catch((error) => {
        const response = error?.response
        const data = response?.data
        if (response?.status === 400 || response?.status === 500) {
          Notification.error(data.message || ' c li xy ra vui lng th li sau')
          return reject(data)
        }
        if (response?.status === 401) {
          Notification.error('Bn cha ng nhp hoc phin lm vic  ht hn')
          sessionStorage.removeItem('token')
          history.push('')
          return
        } else if (response?.body?.message) {
          Notification.error(response.body.message)
        } else {
          Notification.error(' c li xy ra vui lng th li sau')
        }
        reject(data || ' c li xy ra vui lng th li sau')
      })
    })
  }

  clientApi[method.toLowerCase()] = request
})

export const request = clientApi
