import React from 'react'
import CATEGORY from '_helpers/constants'
import { useHistory } from 'react-router-dom'
import classes from './Marketplace.module.scss'

const Marketplace = () => {
  const history = useHistory()
  return (
    <div className={classes.container}
      id='marketplaces'
    >
      <div className={classes.marketplaces}>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>Y C TRUYN</h3>
            { CATEGORY.yct.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => history.push(item.url)}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>DC C TRUYN</h3>
						{ CATEGORY.dct.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => history.push(item.url)}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>Ngun gc xut x</h3>
						{ CATEGORY.cocq.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => {
                  if (item.url.indexOf('http') !== -1) {
                    window.open(item.url)
                  } else {
                    history.push(item.url)
                  }
                  
                }}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>Qun l hnh ngh</h3>
						{ CATEGORY.ql.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => history.push(item.url)}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>Thc hnh tt GxP</h3>
						{ CATEGORY.ht.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => history.push(item.url)}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
        <div className={classes.card}>
          <div className={classes.cardConent}>
						<h3 className={classes.cardTitle}>Qun l cht lng</h3>
						{ CATEGORY.networks.cate.map((item, i) => (
              <div className={classes.btn}
                key={i}
                onClick={() => history.push(item.url)}
              >
                { item.name }
              </div>
            )) }
					</div>
        </div>
      </div>
    </div>
  )
}

export default Marketplace
