import React from 'react'
import { Row, Col, Container } from 'reactstrap'
import logo from '_assets/no-image.jpg'
import { useHistory } from 'react-router-dom'
import { FileURL } from '_helpers/constants'
import './style.scss'
import classNames from 'classnames'
// import dayjs from 'dayjs'

function Item({data, showDes, goDetail, noIm, paddingContent}) {
  const history = useHistory()
  function goToDetail() {
    history.push(`/tin-tuc/${data.newS_ID}`)
  }
  return (
    <Container className='mb-2 text-justify p-0' style={{ cursor: 'pointer' }} onClick={goDetail || goToDetail}>
      <Row>
        <Col xs={12}>
          {
            !noIm &&
            <img className='w-100' src={ data.avatar ? `${FileURL}/${data.avatar}` : logo} alt='anh' />
          }
          <div>
          {
            noIm && data.avatar &&
            <img className='w-100' src={data.avatar.indexOf('http') !== -1 ? data.avatar : `${FileURL}/${data.avatar}`} alt='anh' />
          }
          <div className={classNames('mt-2 news-item-title', paddingContent && 'ph20')}>{data.title}</div>
          {
            showDes &&
            <div className={classNames('mt-2', paddingContent && 'ph20' )} dangerouslySetInnerHTML={{ __html: data.shorT_CONTENT || data.desc }} />
          }
          </div>
          
        </Col>
      </Row>
    </Container>
  )
}
export default Item
