import React from 'react'
import './style.scss'
import { Input, Label, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap'
const errorStyle = {
    left: '10px',
    fontSize: '11px',
    color: 'red',
}
const CustomField = ({ children, label, note, required, defaultValue, className, input, onChange, meta: { touched, error, warning }, ...props }) => {
    switch (input.type) {
        case 'select':
            return (
                <div className='new-field'>
                    <br />
                    <Input {...input} className={className} {...props}>
                        {children}
                    </Input>
                    <Label>{label}</Label>
                    {touched && ((error && <span style={errorStyle}>{error}</span>) || (warning && <span style={errorStyle}>{warning}</span>))}
                </div>
            );
        case 'textarea':
            return (
                <div className='text-left new-field'>
                    <br />
                    <Input {...input}  type={'textarea'} className={className} {...props} />
                    <Label>{label}</Label>
                    {touched && ((error && <span style={errorStyle}>{error}</span>) || (warning && <span style={errorStyle}>{warning}</span>))}
                </div>
            );
        default:
            return (
                <div className='text-left new-field'>
                    <br />
                    <InputGroup>
                      <Input {...input} onChange={(v) => { input.onChange(v); if (onChange) { onChange(v)} }} className={className} {...props} />
                      {
                        props.vnd &&
                        <InputGroupAddon addonType="append">
                          <InputGroupText style={{ fontSize: '14px' }}>VND</InputGroupText>
                        </InputGroupAddon>
                      }

                    </InputGroup>
                    <Label>
                      {label}
                      { note && <label style={{ fontSize: '12px', color: '#c1c6ca' }}>({note})</label>}
                      { required && <label style={{ fontSize: '12px', color: 'red' }}> &nbsp;* </label>}
                    </Label>
                    {touched && ((error && <span style={errorStyle}>{error}</span>) || (warning && <span style={errorStyle}>{warning}</span>))}
                </div>
            );
    }
};

export default CustomField;
