import React, { useEffect, useState } from "react";
import NewsVideo from "../pages/Home/NewsVideo/index.js";
import logoheader from "./Logo-header.png";
import logofooter from "./logo-footer.png";
import searchIcon from "../_assets/search.svg";
import "./custom.scss";
import { useHistory } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  InputGroup,
  Input,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { request, APP_API1 } from "_helpers/request";
import qs from "qs";
import Slider from "react-slick";
import SideBar from "./SideBar";
import Item from "_components/news-item";

const settings = {
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 6,
  slidesToScroll: 1,
  autoplay: true,
  autoplaySpeed: 1000,
  arrows: false,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 1,
      },
    },
    {
      breakpoint: 767,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1,
      },
    },
  ],
};

let timeout = null;

function PrivateRouter(props) {
  // const [menu, setMenu] = useState([])

  const [isOpen, setIsOpen] = useState(false);
  const [totalTitle, setTotalTitle] = useState("");
  const [news, setNews] = useState([]);
  const [config, setConfig] = useState({});
  const history = useHistory();

  const toggle = () => setIsOpen(!isOpen);

  useEffect(() => {
    request.get("api/confugration").then((res) => {
      var obj = res.reduce(function (acc, cur) {
        acc[cur.confugratioN_CODE] = cur;
        return acc;
      }, {});
      setConfig(obj);
    });
    if (props.dct) {
      request.get(`api/news/top-hot/6`).then((res) => setNews(res));
    }
  }, []);

  useEffect(() => {
    const params = qs.parse(props.location.search, {
      ignoreQueryPrefix: true,
    });

    if (params.text !== totalTitle) {
      setTotalTitle(params.text);
    }
  }, [props.location.search]);

  const mailto = `mailto:${config?.EMAIL?.value}`;
  const tel = `tel:${config?.PHONE?.value}`;
  return (
    <React.Fragment>
      <Container fluid style={{ backgroundColor: "#2391d0" }}>
        <Container fluid>
          <Navbar expand="md" color="faded" light className="navbar-header">
            <NavbarBrand
              onClick={() => history.push("/")}
              style={{ cursor: "pointer" }}
              className="logoWrapper"
            >
              <img
                style={{ cursor: "pointer" }}
                src={logoheader}
                alt="header"
                className="mr-2 logo-header"
              />
              <div>
                <b className="h-t1"> B Y T </b>
                <b className="h-t2"> CC QUN L, Y DC C TRUYN</b>
              </div>
            </NavbarBrand>
            <NavbarToggler onClick={toggle} />
            <Collapse isOpen={isOpen} navbar>
              <Nav className="nav-header" navbar>
                {props?.menu
                  ?.sort((a, b) => a.ordeR_ID - b.ordeR_ID)
                  .map((e, i) => {
                    if (!e.submenu || e.submenu.length === 0) {
                      return (
                        <NavItem key={i}>
                          <NavLink
                            style={{ whiteSpace: "nowrap" }}
                            href={e.urL_NAME}
                          >
                            {e.desc}
                          </NavLink>
                        </NavItem>
                      );
                    }
                    return (
                      <UncontrolledDropdown key={i} nav inNavbar>
                        <DropdownToggle
                          nav
                          caret
                          className="text"
                          onClick={() => history.push(`/${e.urL_NAME}`)}
                        >
                          {e.desc}
                        </DropdownToggle>
                        <DropdownMenu center>
                          {e.submenu
                            .sort((a, b) => a.ordeR_ID - b.ordeR_ID)
                            .map((e1, i) => (
                              <DropdownItem
                                key={i}
                                onClick={() => {
                                  if (
                                    e1.urL_NAME.indexOf("http://") !== -1 ||
                                    e1.urL_NAME.indexOf("https://") !== -1
                                  ) {
                                    window.open(e1.urL_NAME);
                                  } else {
                                    history.push(`/${e1.urL_NAME}`);
                                  }
                                }}
                                className={"dropdown-item"}
                              >
                                <i
                                  className="fa fa-caret-right"
                                  aria-hidden="true"
                                />{" "}
                                &nbsp;{e1.description}
                              </DropdownItem>
                            ))}
                        </DropdownMenu>
                      </UncontrolledDropdown>
                    );
                  })}
              </Nav>
            </Collapse>
          </Navbar>
        </Container>
      </Container>

      {/* {!props.about
          && <Container fluid className='banner'>
          {
          <div className='inputWrapper'>
            <InputGroup>
              <Input value={totalTitle}
                id='totalTitle'
                onChange={(e) => {
                  setTotalTitle(e.target.value)
                  clearTimeout(timeout)
                  timeout = setTimeout(() => {
                    history.push(`/search?text=${e.target.value || ''}`)
                  }, 1000)
                }}
                placeholder="Tm kim"
                className='input'
                onKeyDown={(e) => {
                  if (e.which === 13) {
                    history.push(`/search?text=${totalTitle || ''}`)
                  }

                }}
              />
              <img src={searchIcon} className={'searchIcon'} alt='searchIcon'/>
            </InputGroup>
          </div>
        }
        <div className='video-2'>
          <NewsVideo />
        </div>
      </Container>
        } */}

      {/* <Container className='pt-1 pb-1 d-flex align-items-center justify-content-between hed'>
          <div className='pt-1 pb-1 d-flex align-items-center'>
            <div className='d-flex'>
              <img onClick={() => history.push('/')} style={{ cursor: 'pointer' }} src={logoheader} alt='header' className='mr-2' />
              <div className='d-flex align-items-center border-right mr-2 pr-2' onClick={() => history.push('/')} style={{ cursor: 'pointer' }}>
                <div>
                  <div><b className='h-t1'> CC QUN L </b></div>
                  <div><b className='h-t2'> Y DC C TRUYN </b></div>
                </div>
              </div>
              <div className='d-flex hedc'>
                <div className='d-flex align-items-center border-right mr-2 pr-2'>
                  <i className="fa fa-envelope" aria-hidden="true" style={{ color: '#ed1c24'}}></i>
                  &nbsp;  &nbsp;
                  <a style={{ color: 'rgba(0, 0, 0, 0.87)' }} href={mailto}>{config?.EMAIL?.value}</a>

                </div>
                <div className='d-flex align-items-center mr-2 pr-2'>
                  <i className="fa fa-phone mr-2" aria-hidden="true" style={{ color: '#ed1c24'}}></i>
                  &nbsp;
                  <a style={{ color: 'rgba(0, 0, 0, 0.87)' }} href={tel}>{config?.PHONE?.value}</a>
                </div>
              </div>
            </div>
          </div>


        </Container> */}
      {props.home && props.children}
      {!props.home && (
        <Container>
          <Row>
            <Col md={3} className="d-md-block d-none">
              <SideBar menu={props.menu} />
            </Col>
            <Col md={9}>{props.children}</Col>
          </Row>
        </Container>
      )}
      {props.dct && (
        <Container>
          <Row>
            <Col xs={12}>
              <div className="p-2 mb-3 mt20">
                <b className="text-title">
                  Nhng bi vit nhiu ngi truy cp
                </b>
              </div>
            </Col>
            {news.map((e, i) => (
              <Col md={4} key={i}>
                <Item data={e} showDes={true} />
              </Col>
            ))}
          </Row>
        </Container>
      )}

      <Container fluid className="p-3 link-wrapper">
        <Container className="d-flex justify-content-space-between p-3 link-wrapper-content">
          <div className="img__wrap">
            <img
              className="img__img"
              style={{ width: "84px", height: "84px" }}
              alt="logo"
              src={logofooter}
            />
            <div className="img__description">
              Cc Qun L Y, Dc C Truyn
            </div>
          </div>
          <div className={"sliders"}>
            <Slider {...settings}>
              {props.links.map((e, i) => (
                <div className="img__wrap text-center" key={i}>
                  <img
                    className="img__img"
                    style={{ maxWidth: "100%", margin: "0 auto" }}
                    key={i}
                    alt={e.desc}
                    src={`${APP_API1}${e.avatar}`}
                    onClick={() => window.open(e.urL_LINK, "_blank")}
                  />
                  <div className="img__description">{e.desc}</div>
                </div>
              ))}
            </Slider>
          </div>
        </Container>
      </Container>
      <Container
        fluid
        style={{ backgroundColor: "#2391d0", paddingTop: "40px" }}
      >
        <Container className="pt-3 pb-3 footer">
          <Row>
            <Col md={3} className="text-center text-light">
              <img src={logofooter} alt="footer" />
              {/* <div className='mt-2'><b className='f-t1'> CC QUN L </b> </div>
                <div><b> Y DC C TRUYN </b> </div> */}
            </Col>
            <Col md={4} className="text-left text-light">
              <div>
                <b className="f-t1"> Lin H</b>{" "}
              </div>
              <div className="mb10">
                <i className="fa fa-map-marker" aria-hidden="true"></i> &nbsp;
                a ch: {config?.ADDRESS?.value}
              </div>
              <div className="mb10">
                <i className="fa fa-phone" aria-hidden="true"></i> &nbsp; in
                thoi:{" "}
                <a style={{ color: "#fff" }} href={tel}>
                  {config?.PHONE?.value}
                </a>
              </div>
              <div className="mb10">
                <i className="fa fa-fax" aria-hidden="true"></i> &nbsp; Fax:{" "}
                {config?.FAX?.value}
              </div>

              <div className="mb10">
                <i className="fa fa-envelope" aria-hidden="true"></i> &nbsp;
                Email:{" "}
                <a style={{ color: "#fff" }} href={mailto}>
                  {config?.EMAIL?.value}
                </a>{" "}
              </div>
            </Col>
            <Col md={5} className="text-left text-light">
              <div>
                <b className="f-t1">Danh mc</b>{" "}
              </div>
              <Row>
                {props.menu.map((e, i) => (
                  <Col md={6} key={i}>
                    <div
                      onClick={() => history.push(`/${e.urL_NAME}`)}
                      className="link"
                    >
                      {" "}
                      <span className="footer-text">{e.desc}</span>
                    </div>
                  </Col>
                ))}
              </Row>
            </Col>
          </Row>
          <p className="copyright">
            Copyright  2021 by ydct.moh.gov.vn - All Rights Reserved.
          </p>
        </Container>
      </Container>
    </React.Fragment>
  );
}

export default PrivateRouter;
