import React, { useEffect, useState } from "react";
import PrivateRouter from "layout/privateRouter";
import {
  Row,
  Col,
  Container,
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from "reactstrap";
import { useHistory } from "react-router-dom";
import Item from "./section1/item.js";
import { request } from "_helpers/request";
// import Paging from '_components/paging'
import NewsAndVideos from "./NewsAndVideos";
import NewsAndCats from "./newsAndCats";
import SearchBar from "./SearchBar";
import Section2 from "./section2/index.js";
import Section3 from "./section3/index.js";
import "./style.scss";
import { FileURL } from "_helpers/constants";
import banner from "_assets/banner.jpg";

function Home(props) {
  const history = useHistory();
  const [hotNews, setHotNews] = useState([]);
  const [page, setPage] = useState(1);
  const [newsObj, setNewsObj] = useState([]);
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);
  useEffect(() => {
    request.get("api/news/top-hot/3").then((res) => setHotNews(res));
  }, []);

  useEffect(() => {
    const params = { page: page, size: 3, isHot: "N" };
    request
      .get(`api/news/client-search`, { params })
      .then((res) => setNewsObj(res));
  }, []);
  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === hotNews.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? hotNews.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  };
  function goToDetail(e) {
    history.push(`/tin-tuc/${e.newS_ID}`);
  }
  function search(p) {
    setPage(p + 1);
    const params = { page: p + 1, size: 3, isHot: "N" };
    request
      .get(`api/news/client-search`, { params })
      .then((res) => setNewsObj(res));
  }

  return (
    <PrivateRouter home {...props}>
      <SearchBar />
      <NewsAndCats />
      <Section2 />

      <NewsAndVideos />
      {/* <div className="bg-white text-center section0">
        <Container>
          <div className="row-between">
            <h3 className="news-new">Bi vit ni bt</h3>
            <div className={"viewall"} onClick={() => history.push("/search")}>
              Xem tt c
            </div>
          </div>
          <Row>
            <Col md={7} className="mb-3">
              <Carousel
                activeIndex={activeIndex}
                next={next}
                previous={previous}
              >
                <CarouselIndicators
                  items={hotNews}
                  activeIndex={activeIndex}
                  onClickHandler={goToIndex}
                />
                {hotNews.map((item, i) => (
                  <CarouselItem
                    onExiting={() => setAnimating(true)}
                    onExited={() => setAnimating(false)}
                    key={i}
                  >
                    <img
                      className="w-100"
                      src={item.avatar ? `${FileURL}/${item.avatar}` : banner}
                      alt={item.altText}
                    />
                    <div
                      onClick={() => goToDetail(item)}
                      style={{ cursor: "pointer" }}
                    >
                      <CarouselCaption captionText={item.title} />
                    </div>
                  </CarouselItem>
                ))}
                <CarouselControl
                  direction="prev"
                  directionText="Previous"
                  onClickHandler={previous}
                />
                <CarouselControl
                  direction="next"
                  directionText="Next"
                  onClickHandler={next}
                />
              </Carousel>
            </Col>
            <Col md={5}>
              {newsObj?.results?.map((e, i) => (
                <Item key={i} data={e} />
              ))}
            </Col>
          </Row>
        </Container>
      </div> */}
      {/* <Section3 /> */}
    </PrivateRouter>
  );
}

export default Home;
